import React from 'react';
import spinner from './spinner.gif';

export default function Spinner() {
  return (
    <div>
      <img
        src={spinner}
        alt="Loading..."
        style={{
          width: '60px',
          margin: 'auto',
          display: 'block',
          paddingTop: '4em'
        }}
      />
    </div>
  );
}